<template>
  <div>
    <el-dialog :title="dialogUser.title" :visible.sync="dialogUser.show" :close-on-click-modal='false'
    :close-on-press-escape='false' :modal-append-to-body="false" width="80%" top="0">
      <div class="form">
        <el-form :model="caseForm" ref="caseForm" :rules="rules" style="margin:10px;" label-width="120px">
          <el-form-item label="" prop="id" style="display:none" >
            <el-input v-model="caseForm.id" disabled></el-input>
          </el-form-item>

          <el-form-item label="" prop="create_user" style="display:none">
            <el-input v-model="caseForm.create_user" disabled></el-input>
          </el-form-item>

          <el-form-item label="" prop="create_time" style="display:none">
            <el-input v-model="caseForm.create_time" disabled></el-input>
          </el-form-item>

          <el-form-item label="" prop="title">
            <el-input v-model="caseForm.title" placeholder=""></el-input>
          </el-form-item>

          <el-form-item label="" prop="cover_photo">
            <el-input v-model="caseForm.cover_photo" placeholder="(URL)"></el-input>
          </el-form-item>

          <el-form-item label="" prop="article">
            <ckeditor v-model="caseForm.article" :config="editorConfig" style="margin-bottom:5px"></ckeditor>
          </el-form-item>

          <el-form-item>
              <el-button @click="dialogUser.show = false"></el-button>
              <el-button type="primary" @click='onSubmit("caseForm")'></el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { db } from '../firebase'
export default {
  inject: ['reload'],
  props: {
    dialogUser: Object,
    caseForm: Object
  },
  data () {
    return {
      editorData: '',
      editorConfig: {
        height: '230px'
      },
      county: [],
      district: [],
      rules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 100, message: '6~30', trigger: 'blur' }
        ],
        article: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, message: '6~1500', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    onSubmit (caseForm) {
      // this.$refs[form.validate]
      this.$refs[caseForm].validate(valid => {
        // validtruefalse
        if (valid) {
          // Firebase
          if (this.dialogUser.option === 'add') {
            db.database().ref('bulletin').child(this.caseForm.id).set({
              id: this.caseForm.id,
              create_user: this.caseForm.create_user,
              create_time: this.caseForm.create_time,
              title: this.caseForm.title,
              cover_photo: this.caseForm.cover_photo,
              article: this.caseForm.article
            })
          } else if (this.dialogUser.option === 'edit') {
            console.log(this.caseForm.id)
            db.database().ref('bulletin').child(this.caseForm.id).update({
              title: this.caseForm.title,
              cover_photo: this.caseForm.cover_photo,
              article: this.caseForm.article
            })
          }
          this.$message({
            message: '',
            type: 'success'
          })
          // 
          this.dialogUser.show = false
          // 
          this.reload()
        }
      })
    }
  }
}
</script>
