<template>
  <div>
    <el-dialog :title="dialogUser.title" :visible.sync="dialogUser.show" :close-on-click-modal='false'
    :close-on-press-escape='false' :modal-append-to-body="false">
      <div class="form">
        <el-form :model="registerUser" ref="registerUser" :rules="rules" label-width="120px" style="margin:10px;width:auto;">
          <el-form-item label="" prop="id" style="display:none">
            <el-input v-model="registerUser.id" disabled></el-input>
          </el-form-item>

          <el-form-item label="" prop="create_user" style="display:none">
            <el-input v-model="registerUser.create_user" disabled></el-input>
          </el-form-item>

          <el-form-item label="" prop="create_time" style="display:none">
            <el-input v-model="registerUser.create_time" disabled></el-input>
          </el-form-item>

          <el-form-item label="" prop="enabled" style="display:none">
            <template slot-scope="scope">
              <el-switch v-model="registerUser.enabled"  @change="editEnabled(scope.row)"
              :active-value="false" :inactive-value="true" active-color="#f56c6c" inactive-color="#409EFF"></el-switch>
            </template>
          </el-form-item>

            <el-form-item label="" prop="emp_name">
              <el-input v-model="registerUser.emp_name" type="name" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="email">
              <el-input v-model="registerUser.email" type="name" placeholder=""></el-input>
            </el-form-item>

          <el-form-item v-if="this.dialogUser.option == 'add'" label="" prop="password">
            <el-input v-model="registerUser.password" placeholder="" type="password" show-password></el-input>
          </el-form-item>
          <el-form-item v-if="this.dialogUser.option == 'add'" label="" prop="password2">
            <el-input v-model="registerUser.password2" placeholder="" type="password" show-password></el-input>
          </el-form-item>

            <el-form-item label="" prop="dept">
              <el-select v-model="registerUser.dept" placeholder="">
                <el-option v-for="item in departmentList" :key="item" :label="item" :value="item"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="" prop="shift">
              <el-select v-model="registerUser.shift" placeholder="">
                <el-option v-for="item in shiftList" :key="item" :label="item" :value="item"></el-option>
              </el-select>
            </el-form-item>

            <el-form-item label="" prop="identity">
              <el-select v-model="registerUser.identity" placeholder="">
                <el-option v-for="item in roleList" :key="item.id" :label="item.role_name" :value="item.role_name"></el-option>
              </el-select>
            </el-form-item>
          <el-form-item>
              <el-button @click="dialogUser.show = false"></el-button>
              <el-button type="primary" @click='onSubmit("registerUser")'></el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { db } from '../firebase'
export default {
  inject: ['reload'],
  props: {
    dialogUser: Object,
    registerUser: Object
  },
  data () {
    var validatePass2 = (rule, value, callback) => {
      if (value !== this.registerUser.password) {
        callback(new Error(''))
      } else {
        callback()
      }
    }
    return {
      departmentList: ['', '', '', ''],
      shiftList: ['', '', ''],
      roleList: [],
      rules: {
        emp_name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 30, message: '', trigger: 'blur' }
        ],
        email: [
          { type: 'email', required: true, message: 'Email', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 30, message: '6~30', trigger: 'blur' }
        ],
        password2: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 30, message: '6~30', trigger: 'blur' },
          { validator: validatePass2, trigger: 'blur' }
        ],
        dept: [
          { required: true, message: '', trigger: ['blur', 'change'] }
        ],
        shift: [
          { required: true, message: '', trigger: ['blur', 'change'] }
        ],
        identity: [
          { required: true, message: '', trigger: ['blur', 'change'] }
        ]
      }
    }
  },
  methods: {
    getRole () {
      db.database().ref('role').once('value', (snapshot) => {
        snapshot.forEach(element => {
          this.roleList.push(element.val())
        })
      })
    },
    onSubmit (registerUser) {
      // this.$refs[form.validate]
      this.$refs[registerUser].validate(valid => {
        // validtruefalse
        if (valid) {
          if (this.dialogUser.option === 'add') {
            db.database().ref('user').child(this.registerUser.id).set({
              id: this.registerUser.id,
              create_time: this.registerUser.create_time,
              create_user: this.registerUser.create_user,
              emp_name: this.registerUser.emp_name,
              enabled: this.registerUser.enabled,
              email: this.registerUser.email,
              password: this.registerUser.password,
              password2: this.registerUser.password2,
              dept: this.registerUser.dept,
              shift: this.registerUser.shift,
              identity: this.registerUser.identity
            })
          } else if (this.dialogUser.option === 'edit') {
            db.database().ref('user').child(this.registerUser.id).update({
              emp_name: this.registerUser.emp_name,
              email: this.registerUser.email,
              dept: this.registerUser.dept,
              shift: this.registerUser.shift,
              identity: this.registerUser.identity
            })
          }
          this.$message({
            message: '',
            type: 'success'
          })
          this.dialogUser.show = false
          // 
          this.reload()
        }
      })
    }
  },
  created () {
    this.getRole()
  }
}
</script>
