import Vue from 'vue'

// 
import HeadNav from '../components/HeadNav'
import Footer from '../components/Footer'
import ScrollTop from '../components/ScrollTop'

// 
import Index from '../views/Index.vue'
import About from '../views/About.vue'

import News from '../views/news/News.vue'
import NewsLayout from '../views/news/NewsLayout.vue'
import NewsInfo from '../views/news/NewsInfo.vue'

import Contact from '../views/Contact.vue'

import Properties from '../views/properties/Properties.vue'
import PropertiesLayout from '../views/properties/PropertiesLayout.vue'
import PropertiesInfo from '../views/properties/PropertiesInfo.vue'

import Staff from '../views/Staff.vue'
import Privacy from '../views/Privacy.vue'

// 
import Backstage from '../views/Backstage.vue'
import BackendHome from '../views/create/BackendHome.vue'
import Emp from '../views/create/Emp.vue'
import Role from '../views/create/Role.vue'
import CaseList from '../views/create/CaseList.vue'
import NewsList from '../views/create/NewsList.vue'
import BulletinList from '../views/create/BulletinList.vue'
import BulletinInfo from '../views/create/BulletinInfo.vue'

import NotFoundComponent from '../views/NotFoundComponent.vue'
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

import VueRouter from 'vue-router'

Vue.use(ElementUI)
Vue.use(BootstrapVue)
Vue.use(IconsPlugin)
Vue.use(VueRouter)

const routes = [
  {
    path: '*',
    component: NotFoundComponent
  },
  {
    path: '/privacy',
    components: {
      header: HeadNav,
      main: Privacy,
      footer: Footer
    },
    meta: {
      title: ''
    }
  },
  {
    path: '/',
    components: {
      header: HeadNav,
      main: Index,
      footer: Footer,
      scrollTop: ScrollTop
    },
    meta: {
      title: ''
    }
  },
  {
    path: '/about',
    components: {
      header: HeadNav,
      main: About,
      footer: Footer,
      scrollTop: ScrollTop
    },
    meta: {
      title: ''
    }
  },
  {
    path: '/news',
    components: {
      header: HeadNav,
      main: News,
      footer: Footer,
      scrollTop: ScrollTop
    },
    children: [
      {
        path: '',
        component: NewsLayout,
        meta: {
          title: ''
        }
      },
      {
        path: ':id',
        component: NewsInfo,
        meta: {
          title: ''
        }
      }
    ]
  },
  {
    path: '/contact',
    components: {
      header: HeadNav,
      main: Contact,
      footer: Footer,
      scrollTop: ScrollTop
    },
    meta: {
      title: ''
    }
  },
  {
    path: '/case',
    components: {
      header: HeadNav,
      main: Properties,
      footer: Footer,
      scrollTop: ScrollTop
    },
    children: [
      {
        path: '',
        component: PropertiesLayout,
        meta: {
          title: ''
        }
      },
      {
        path: ':id',
        component: PropertiesInfo,
        meta: {
          title: ''
        }
      }
    ]
  },
  {
    path: '/users',
    components: {
      header: HeadNav,
      main: Staff,
      footer: Footer
    },
    meta: {
      title: ''
    }
  },
  {
    path: '/backstage',
    components: {
      header: HeadNav,
      main: Backstage
    },
    meta: {
      title: '',
      requiresAuth: true
    },
    children: [
      {
        path: '',
        component: BackendHome,
        meta: {
          title: ''
        }
      },
      {
        path: '/bulletin/:id',
        component: BulletinInfo,
        meta: {
          title: ''
        }
      },
      {
        path: '/role',
        component: Role,
        meta: {
          title: ''
        }
      },
      {
        path: '/emp',
        component: Emp,
        meta: {
          title: ''
        }
      },
      {
        path: '/caseList',
        component: CaseList,
        meta: {
          title: ''
        }
      },
      {
        path: '/newsList',
        component: NewsList,
        meta: {
          title: ''
        }
      },
      {
        path: '/bulletinList',
        component: BulletinList,
        meta: {
          title: ''
        }
      }
    ]
  }
]

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  const isLogin = localStorage.getItem('operationType') === 'signIn'
  if (to.meta.title) {
    document.title = to.meta.title
    if (to.meta.requiresAuth) {
      if (isLogin) {
        next()
      } else {
        alert('')
        router.push('/users')
      }
    } else {
      next()
    }
  }
})

router.afterEach((to, from, next) => {
  window.scrollTo({
    top: 0,
    behavior: 'smooth'
  })
})

export default router
